* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Inter";
  background-color: rgb(100, 100, 100);
}

.nav--logo {
  width: 100px;
  margin-right: 0px;
}

.nav-items {
  display: flex;
  align-items: center;
  background-color: #1c1e25;
  padding: 30px 25px;
  height: 90px;
}

.nav--logo_text,
.nav--title {
  margin: 0;
}

.nav--logo_text {
  color: #61dafb;
  margin-right: auto;
  font-weight: bold;
  font-size: xx-large;
}

.nav--title {
  color: #ffffff;
  font-weight: 700;
}

.main-items {
  padding: 27px 27px;
  background-color: #23272d;
  color: #ffffff;
  background-repeat: repeat-y;
  background-position: 384px 25%;
  transition: all 0.3s ease-in-out;
  border-radius: 20px;
  margin: 30px;
  border: 1px solid white;
  box-shadow: 0px 0px 20px 10px #4b525e;
}

.main-items:hover {
  /* margin: 25px; */
  margin: 57px;
  margin-top: 60px;
  transform: scale(1.04);
  box-shadow: 0px 0px 20px 10px #2f333a;
}

.main--text {
  font-weight: bold;
  font-size: 39.06px;
  letter-spacing: -0.02em;
  font-style: normal;
  font-stretch: normal;
  text-decoration: line-through;
  padding: 0px 20px 20px 20px;
}

.main--ls {
  list-style: none;
  font-size: larger;
  max-width: 500px;
}

.main--ls > li {
  line-height: 19px;
  padding-block: 8px;
}

.main--ls > li::before {
  content: "\2022";
  color: #61dafb;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
  font-size: 2rem;
}

.joke {
  list-style: none;
}

@keyframes logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .nav--logo {
    -webkit-animation: logo-spin 10s linear infinite;
    animation: logo-spin 10000ms linear infinite;
  }
}
